import { Helmet } from 'react-helmet-async';
// react
import { useMemo, useState } from 'react';
// MUI
import LoadingButton from '@mui/lab/LoadingButton/LoadingButton';
import Button from '@mui/material/Button';
import Card from '@mui/material/Card';
import Grid from '@mui/material/Grid';
import Stack from '@mui/material/Stack';
import TextField from '@mui/material/TextField';
// form
import { yupResolver } from '@hookform/resolvers/yup';
import { useForm } from 'react-hook-form';
import * as Yup from 'yup';
// component
import CustomBreadcrumbs from 'src/components/custom-breadcrumbs';
import FormProvider, { BlockItem } from 'src/components/hook-form';
import { useSnackbar } from 'src/components/snackbar';
import { RouterLink } from 'src/routes/components';
// route
import { paths } from 'src/routes/paths';
// axios
import axios from 'axios';
// key
//
import { useReactMediaRecorder } from 'react-media-recorder';

// ----------------------------------------------------------------------

export default function Page() {
  const { enqueueSnackbar } = useSnackbar();

  const [text, setText] = useState('');

  const Schema = Yup.object().shape({
    file: Yup.mixed(),
  });

  const defaultValues = useMemo(
    () => ({
      file: '',
    }),
    []
  );

  const methods = useForm({
    resolver: yupResolver(Schema),
    defaultValues,
  });

  const {
    handleSubmit,
    setValue,
    formState: { isSubmitting },
  } = methods;

  const onSubmit = handleSubmit(async (data: any) => {
    const formData = new FormData();
    formData.append('file', data.file);

    try {
      if (!data.file) {
        enqueueSnackbar('Bạn chưa upload file giọng nói lên !', { variant: 'warning' });
      } else {
        const response = (await axios.post(
          'https://viettelgroup.ai/voice/api/asr/v1/rest/decode_file',
          formData,
          {
            headers: {
              token: 'anonymous',
              // token: VIETTEL_API_KEY,
            },
          }
        )) as any;

        setText(response?.data[0]?.result?.hypotheses[0]?.transcript);

        enqueueSnackbar('Chuyển đổi thành công thành văn bản !');
      }
    } catch (error) {
      enqueueSnackbar('Đã có lỗi xảy ra ! Vui lòng thử lại !', { variant: 'error' });
    }
  });

  const handleFileChange = async (event: any) => {
    const file = await event.target.files[0];

    const maxSizeInBytes = 1024 * 1024 * 5;

    if (file.size > maxSizeInBytes) {
      enqueueSnackbar('Vui lòng upload tập tin nhỏ hơn 5MB !', { variant: 'warning' });
    } else {
      setValue('file', file);
    }
  };

  // ----------------------------------------------------------------------

  const { status, stopRecording, mediaBlobUrl } = useReactMediaRecorder({
    audio: true,
  });

  const handleDownload = () => {
    if (mediaBlobUrl) {
      const anchor = document.createElement('a');
      anchor.href = mediaBlobUrl;
      anchor.download = 'audio.mp3';
      anchor.click();
    }
  };

  return (
    <>
      <Helmet>
        <title> Chuyển đổi giọng nói thành văn bản </title>
      </Helmet>

      <CustomBreadcrumbs
        heading="Giọng nói thành văn bản"
        links={[
          { name: 'Tiện ích' },
          {
            name: 'Chuyển đổi',
            href: paths.home.convert.root,
          },
          { name: 'Giọng nói thành văn bản' },
        ]}
        sx={{ mb: { xs: 3, md: 5 } }}
      />

      <Card sx={{ padding: 3 }}>
        <FormProvider methods={methods} onSubmit={onSubmit}>
          <Grid container spacing={3}>
            <Grid item xs={12} md={6}>
              <Stack spacing={2} height="100%" justifyContent="space-between">
                <BlockItem label="Tải lên file audio giọng nói ( định dạng .wav, .mp3 ) :" required>
                  <TextField type="file" onChange={handleFileChange} sx={{ width: '100%' }} />
                </BlockItem>

                <Stack direction="row" spacing={1}>
                  {/* <Button variant="contained" color="primary" onClick={startRecording}>
                      Ghi âm giọng nói
                    </Button> */}

                  <Button
                    variant="contained"
                    color="primary"
                    component={RouterLink}
                    href="https://online-voice-recorder.com/vi/"
                    target="_blank"
                  >
                    Ghi âm giọng nói
                  </Button>

                  {status === 'recording' && (
                    <Button variant="contained" color="error" onClick={stopRecording}>
                      Dừng ghi
                    </Button>
                  )}

                  {status === 'stopped' && (
                    <Button variant="contained" color="inherit" onClick={handleDownload}>
                      Tải xuống
                    </Button>
                  )}
                </Stack>

                <LoadingButton
                  type="submit"
                  size="large"
                  color="primary"
                  variant="contained"
                  loading={isSubmitting}
                >
                  CHUYỂN GIỌNG NÓI NÀY THÀNH VĂN BẢN
                </LoadingButton>
              </Stack>
            </Grid>

            <Grid item xs={12} md={6}>
              <BlockItem label="Văn bản sau khi chuyển đổi :" sx={{ width: '100%' }}>
                <TextField
                  value={text}
                  onChange={(event: any) => setText(event.target.value)}
                  multiline
                  rows={6}
                  sx={{ width: '100%' }}
                />
              </BlockItem>
            </Grid>
          </Grid>
        </FormProvider>
      </Card>
    </>
  );
}
